export default (preState = [], action) => {
    const { type, data } = action
    switch (type) {
        case "override_playlist":
            // 
            return data.split(',');
        case "add_playlist":
            //
            for (let i of preState) {
                if (data === i) {
                    return preState
                }
            }
            //,
            return [data, ...preState];

        default:
            return preState
    }

}